<script>
    export default {
        name: "user-view",
        data() {
            return {
                user: {},
                loading: true
            };
        },
        methods : {
            action(action)
            {
                window.action(action , 'Agent' , `${window.location.origin}/admin/portal/agents/${this.$route.params.id}/${action}`).then((response) => {
                    this.user = response.data.body.model;
                });
            },
            init()
            {
                this.loading = true;
                window.axios.get(`${window.location.origin}/admin/portal/agents/${this.$route.params.id}/view`).then((response) => {
                    this.user = response.data.body.model;
                }).finally(() => {
                    this.loading = false;
                });
            },
            del()
            {
                window.action('delete' , 'Agent' , `${window.location.origin}/admin/portal/agents/${this.$route.params.id}/delete`).then((response) => {
                    this.$router.push(`/admin/agents`);
                });
            }
        },
        mounted() {
            this.init();
        }
    }
</script>
<template>
    <div :class="['dimmer' , loading ? 'active' : '']">
        <div class="loader"></div>
        <div class="dimmer-content">
            <div class="row">
                <div class="col-lg-12">
                    <div class="card ribbon-box ">
                        <div class="card-body">
                            <div class="d-flex align-items-center mb-3 flex-wrap">
                                <h4 class="header-title m-0 flex-fill">Personal Information</h4>
                                <div class="">
                                    <button v-if="user.state == 'VERIFIED'" @click="action('deactivate')"  type="button" class="btn btn-light m-1">
                                        <i class="mdi mdi-lightbulb-off-outline mr-1"></i> De-Activate
                                    </button>
                                    <button v-else @click="action('activate')" type="button" class="btn btn-light m-1">
                                        <i class="mdi mdi-lightbulb-outline mr-1"></i> Activate
                                    </button>

                                    <button @click="del" type="button" class="btn btn-light m-1">
                                        <i class="mdi mdi-delete mr-1"></i> Delete
                                    </button>

                                </div>
                            </div>
                            <div class="text-left">
                                <p>
                                    <strong class="mr-2">Wallet :</strong>
                                    <span>{{ user.wallet }}</span>
                                </p>
                                <p>
                                    <strong class="mr-2">First Name :</strong>
                                    <span>{{ user.name }}</span>
                                </p>
                                <p>
                                    <strong class="mr-2">Last Name :</strong>
                                    <span>{{ user.last_name }}</span>
                                </p>
                                <p>
                                    <strong class="mr-2"> Phone :</strong>
                                    <span>{{ user.phone }}</span>
                                </p>
                                <p>
                                    <strong class="mr-2"> ID No :</strong>
                                    <span>{{ user.id_no }}</span>
                                </p>
                                <p>
                                    <strong class="mr-2"> DOB :</strong>
                                    <span>{{ user.dob }}</span>
                                </p>
                                <p>
                                    <strong class="mr-2">Created :</strong>
                                    <span>{{ user.created_at }}</span>
                                </p>
                                <p>
                                    <strong class="mr-2">State :</strong>
                                    <span> <span class="badge badge-light p-2 font-12">{{ user.state }}</span></span>
                                </p>
                                <p>
                                    <strong class="mr-2">ID Image :</strong>
                                    <img style="max-height: 300px"  :src="user.image"/>
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
